ul {
  margin: 0;
  padding: 0 0 56px 0;
  list-style: none;
}
.TodoList{
  padding: 0 50px;
}
p{
  color: black;
  font-size: 20px;
}
b{
  color: tomato;
}
.initialText{
  font-weight: bold;
  font-size: 25px;
}

/* For small devices */
@media screen and (max-width: 500px) {
  p, .initialText{
    font-size: 10px;
    padding: 10px 0;
    font-weight: 700;
  }
  .TodoList-container{
    padding: 0 10px;
  }
}