.TodoItem {
  background-color: rgba(255, 255, 255, 0.9);
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  box-shadow: 30px 30px 30px -20px rgb(0 30 60 / 50%);
  border-radius: 5px;
  color: black;
}

.TodoItem-p {
  color: #1E1E1F;
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;

}

.TodoItem-p--complete {
  text-decoration: line-through;
}

.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 120px;
  width: 80px;
  font-size: 24px;
  font-weight: bold;
  /* background-color: #CCC; */
}

.Icon-check {
  position: absolute;
  left: -10px;
}

.Icon-check--active {
  color: #4caf50;
}

.Icon-delete {
  position: absolute;
  top: -24px;
  right: 0;
  padding: 0px 5px;
}

.Icon-delete:hover {
  color: #e03131;
}

/* For small devices */
@media screen and (max-width: 500px) {

  .TodoItem {
    margin-top: 14px;
  }
  .TodoItem-p {
    margin: 5px 0 5px 5px;
    font-size: 15px;
    line-height: 20px;
    font-weight: 900;
  }

  .Icon-delete {
    top: -35px;
    right: -10px;
    z-index: 9999;
  }
}