.TodoSearch {
  background: rgba(255, 255, 255, 0.3);
  border-radius: 5px;
  border: 0px solid #202329;
  margin: 30px 24px 0px 24px;
  padding: 0px 20px;
  height: 64px;
  width: calc(100% - 100px);
  font-size: 24px;
  text-align: left;
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  color: #1E1E1F;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

.TodoSearch::placeholder {
  color: black;
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
}

.TodoSearch:focus {
  outline-color: #61DAFA;
}

.TodoSearch:disabled {
  opacity: 25%;
}

/* For small devices */
@media screen and (max-width: 500px) {
  .TodoSearch {
    margin: 30px 30px 10px 30px;
    padding: 0px 20px;
    height: 44px;
    font-size: 14px;
    font-weight: 900;
  }


}